import React, { Component } from 'react';
import { Card, CardBody, CardHeader, Col, Row, Input, Button } from 'reactstrap';
import { Redirect } from 'react-router-dom';
import Cookies from 'universal-cookie';
const cookies = new Cookies();

class Question extends Component {
    constructor(props) {
        super(props);
        this.postComment = this.postComment.bind(this);
        this.state = {
            question: null,
            comment: '',
            isLoggedIn: false
        };
    }

    fetchQuestions(qid) {
        fetch('https://missionaustralia-back.herokuapp.com/getQuestion/' + qid + '/' + cookies.get('token'))
            .then(response => response.json())
            .then(resData => {
                console.log(resData)
                if (resData.response === 200) {
                    this.setState({
                        question: (resData.data !== null && resData.data !== undefined ? resData.data : {})
                    });
                } else {
                    alert("Error fetching question")
                }
            });
    }

    componentWillMount() {
        if (cookies.get('token') !== undefined && cookies.get('token') !== null) {
            this.setState({
                isLoggedIn: true
            });
        }
    }

    componentDidMount() {
        this.fetchQuestions(this.props.match.params.questionId);
    }

    updateInputValue(evt) {
        this.setState({
            comment: evt.target.value
        });
    }

    postComment(qid) {
        console.log(this.state.comment);
        const requestOptions = {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ token: cookies.get('token'), comment: this.state.comment })
        };
        fetch('https://missionaustralia-back.herokuapp.com/postComment/' + qid, requestOptions)
            .then(response => response.json())
            .then(resData => {
                console.log(resData);
                window.location.reload();
            });
    }

    render() {
        if (this.state.isLoggedIn) {
            return (
                <div className="animated fadeIn">
                    <Row>
                        <Col>
                            <Card>
                                <CardHeader>
                                    <i className="fa fa-align-justify"></i> Question {this.props.match.params.questionId}
                                </CardHeader>
                                <CardBody>
                                    {this.state.question !== null ?
                                        <div style={{ textAlign: 'center' }}>
                                            <p><b>Title : </b>{this.state.question.title}</p>
                                            <p><b>Description : </b>{this.state.question.description}</p>
                                            <p><b>Priority : </b>{this.state.question.priority}</p>
                                            <p><b>Comments</b></p>
                                            {this.state.question.comments.map((comment, i) => {
                                                return (
                                                    <p><b>{comment.user} : </b>{comment.content}</p>
                                                );
                                            })}
                                            <Input id="comment" size="16" type="text" name="comment" value={this.state.comment} onChange={evt => this.updateInputValue(evt)} />
                                            <br />
                                            <Button onClick={evt => this.postComment(this.props.match.params.questionId)}>Submit comment</Button>
                                        </div>
                                        : "Question Not Found"}
                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                </div>
            );
        } else {
            return <Redirect to='/login' />
        }
    }
}

export default Question;